import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

Vue.config.productionTip = false

const VueScrollTo = require('vue-scrollto');
const SvgInject = require('@iconfu/svg-inject')

import AOS from 'aos'
import 'aos/dist/aos.css'


import VueTimeline from "@growthbunker/vuetimeline";
import Gsap from 'gsap'


Vue.use(VueScrollTo, {
  container: "body",
  duration: 1500,
  easing: "ease-in-out",
  offset: 0,
  force: true,
  cancelable: true,
  onStart: false,
  onDone: false,
  onCancel: false,
  x: false,
  y: true
}, SvgInject)

Vue.use(VueTimeline, {
  theme: "dark"
})

Vue.use(Gsap)


new Vue({
  created () {
    AOS.init()
  },
  router,
  store,
  render: h => h(App)
}).$mount('#app')
