<template>
    <nav class="font_white navbar navbar-dark navbar-expand-lg fixed-top" :class="{nav_scrolled: scrollPosition > 50, 'nav_not_scrolled': scrollPosition < 50}" >
        <div class="container">
            <router-link to="/" class="navbar-brand mr-auto " :class="{font_scrolled: scrollPosition > 50, 'font_not_scrolled': scrollPosition < 50}" >cajan.<span class="font_secondary_color">{{ extension }}</span>{{ charTypeMarker }}</router-link>
            <button class="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                <span class="navbar-toggler-icon"></span>
            </button>
            <div class="collapse navbar-collapse" id="navbarNav">
                <ul class="navbar-nav ml-auto">
                    <li class="nav-item">
                        <a class="nav-link" href="#"><i class="fas fa-lock"></i> CV</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#">Portfolio</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="#">Blogg</a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="https://www.facebook.com/brbsova" target="_blank"><i class="fab fa-facebook-square"></i></a>
                    </li>
                    <li class="nav-item">
                        <a class="nav-link" href="https://www.linkedin.com/in/christoffer-c-aa5732134/" target="_blank"><i class="fab fa-linkedin"></i></a>
                    </li>
                </ul>
            </div>
        </div>
    </nav>
</template>

<script>
export default {
    data() {
        return {
            extension: 'dev',
            charTypeMarker: "|",
            textTimer: '',
            charTypeMarkerTimer: '',
            counter: 0,
            scrollPosition: null
        }
    },
    methods: {
        changeText()  {
            const displayNames = [
                'dev',
                'dev',
                'de',
                'd',
                '',
                '',
                'j',
                'js',
                'js',
                'j',
                '',
                '',
                'h',
                'ht',
                'htm',
                'html',
                'html',
                'htm',
                'ht',
                'h',
                'h',
                '',
                '',
                'd',
                'de',
            ]
            this.extension = displayNames[this.counter]

            if(this.counter === displayNames.length -1) {
                this.counter = 0
            } else {
                this.counter++
            }

        },
        changeMarker()  {
            if (this.charTypeMarker === "|") {
                this.charTypeMarker = ""
            } else {
                this.charTypeMarker = "|"
            }
        },

        updateScroll() {
            this.scrollPosition = window.scrollY
        }
    },
    created() {
        this.textTimer = setInterval(this.changeText, 500)
        this.charTypeMarkerTimer = setInterval(this.changeMarker, 400)
    },
    mounted() {
        window.addEventListener('scroll', this.updateScroll);
    },
    computed: {
        navScrollClass() {
            console.log(this.scrollPosition)
            return this.scrollPosition > 50 ? 'nav_scrolled' : 'nav_not_scrolled'
        }
    }
}
</script>

<style>
    nav {
        font-family: 'Raleway', sans-serif;
        font-weight: bold;
    }

    .nav_scrolled {
        background-color: rgb(27, 27, 47, 0.9);
        transition: 1s;
    }

    .nav_not_scrolled {
        background-color:  rgb(27, 27, 47, 0);
        transition: 1s;
    }

    .font_scrolled {
        font-size: 1.5em !important;
        transition: .5s;
    }

    .font_not_scrolled {
        font-size: 2.5em;
        transition: .5s;
    }

    .navbar-brand {
        color: white;
        font-size: 2.5em;
    }

    .navbar-brand:hover {
        color: white;
    }

    .nav-link {
        color: white;
        border-bottom: 4px solid rgb(228, 63, 90, 0);
    }

    .nav-link:hover {
        color: white !important;
        text-decoration: none;
        border-bottom: 4px solid #e43f5a;
        box-sizing: border-box;
    }
</style>